import React from 'react'
import {  
    Button,
    Card,
    CardActionArea,
    CardActions,
    CardContent,
    CardMedia,
    makeStyles,
    Typography,}from '@mui/material';





const Post = ({img,title}) => {
  return (
   

    <Card sx={{mb:(theme)=>theme.spacing(5)}}>
     <CardActionArea>
      <CardMedia  component="img" sx={{height : { sm:'150',md:'250'}}}
                                             image={img} title="My Post" />
      <CardContent>
          <Typography gutterBottom variant="h5">
            {title}
          </Typography>
          <Typography variant="body2">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Quibusdam
            consectetur earum est.
          </Typography>
        </CardContent>
   
     </CardActionArea>

     <CardActions>
        <Button size="small" color="primary">
          Share
        </Button>
        <Button size="small" color="primary">
          Learn More
        </Button>
      </CardActions>
    </Card>
  )
}

export default Post