
import React ,{useState} from 'react'

import {
  styled,
  Button,
  Container,
  Fab,
  FormControlLabel,
  FormLabel,
  MenuItem,
  Modal,
  Radio,
  RadioGroup,
  Snackbar,
  TextField,
  Tooltip,
  Alert
} from '@mui/material';

import AddIcon from '@mui/icons-material/Add';

const MyContainer = styled(Container)(({theme}) => ({
  width: 500,
  height: 550,
  backgroundColor: "white",
  position: "absolute",
  top: 0,
  bottom: 0,
  left: 0,
  right: 0,
  margin: "auto",

  [theme.breakpoints.down("sm")]: {
    width: "100vw",
    height: "100vh",
  },
}));

const MyItem = styled("div")(({theme}) => ({
  marginBottom: theme.spacing(3) , 
}));

// function Alert(props) {
//   return <MuiAlert elevation={6} variant="filled" {...props} />;
// }

const Add = () => {
    const [open, setOpen] = useState(false);
    const [openAlert, setOpenAlert] = useState(false);


    const handleClose = (event, reason) => {
      if (reason === "clickaway") {
        return;
      }  
      setOpenAlert(false);
    };

  return (
    <>
     <Tooltip title="Add" aria-label="add" onClick={()=>setOpen(true)}>
        {/* <Fab color="primary" sx={{position : "fixed" , bottom:'20' , right : '20'}}> */}
        <Fab color="primary" sx={{ position: 'fixed' , bottom:'20px' , right:'20px'}}>
            <AddIcon/>
        
        </Fab>
    </Tooltip>


    <Modal open={open}>
        <MyContainer >
          <form sx={{ padding :(theme)=>theme.spacing(2)}} autoComplete="off">
            <MyItem >
              <TextField
                id="standard-basic"
                label="Title"
                size="small"
                style={{ width: "100%" }}
              />
            </MyItem>
            <MyItem >
              <TextField
                id="outlined-multiline-static"
                multiline
                rows={4}
                defaultValue="Tell your story..."
                variant="outlined"
                label="Description"
                size="small"
                style={{ width: "100%" }}
              />
            </MyItem>


            <MyItem >
              <TextField select label="Visibility" value="Public">
                <MenuItem value="Public">Public</MenuItem>
                <MenuItem value="Private">Private</MenuItem>
                <MenuItem value="Unlisted">Unlisted</MenuItem>
              </TextField>
            </MyItem>

            <MyItem >
              <FormLabel component="legend">Who can comment?</FormLabel>
              <RadioGroup>
                <FormControlLabel
                  value="Everybody"
                  control={<Radio size="small" />}
                  label="Everybody"
                />
                <FormControlLabel
                  value="My Friends"
                  control={<Radio size="small" />}
                  label="My Friends"
                />
                <FormControlLabel
                  value="Nobody"
                  control={<Radio size="small" />}
                  label="Nobody"
                />
                <FormControlLabel
                  value="Custom"
                  disabled
                  control={<Radio size="small" />}
                  label="Custom (Premium)"
                />
              </RadioGroup>
            </MyItem>


            <MyItem >
              <Button
                variant="outlined"
                color="primary"
                sx={{ marginRight: 20 }}
                onClick={() => setOpenAlert(true)}
              >
                Create
              </Button>
              <Button
                variant="outlined"
                color="secondary"
                onClick={() => setOpen(false)}
              >
                Cancel
              </Button>
            </MyItem>
          </form>
        </MyContainer>
      </Modal>
      <Snackbar
        open={openAlert}
        autoHideDuration={4000}
        onClose={handleClose}
        anchorOrigin={{ vertical: "bottom", horizontal: "left" }}
      >
        <Alert onClose={handleClose} severity="success">
          This is a success message!
        </Alert>
      </Snackbar>
    </>   
  )
}

export default Add