import React from 'react'
import { Container,Typography,styled } from '@mui/material';
import HomeIcon from '@mui/icons-material/Home';
import BookmarkIcon from '@mui/icons-material/Bookmark';
import ListIcon from '@mui/icons-material/List';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import PersonIcon from '@mui/icons-material/Person';
import PhotoCameraIcon from '@mui/icons-material/PhotoCamera';
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import SettingsIcon from '@mui/icons-material/Settings';
import StorefrontIcon from '@mui/icons-material/Storefront';
import TabletMacIcon from '@mui/icons-material/TabletMac';


const MyContainer = styled(Container)(({theme}) => ({
    height : "100vh",
    color : 'white' ,
    paddingTop : theme.spacing(10) ,
    backgroundColor : theme.palette.primary.main ,  
    [theme.breakpoints.up("sm")]: {
       backgroundColor : "white",
       color:"#555", 
       border : "1px solid #ece7e7"
      }
}));

const MyHomeIcon = styled(HomeIcon)(({theme}) => ({
  marginRight : theme.spacing(1) ,
  [theme.breakpoints.up("sm")]: {
   fontSize : "18px",
 },   
}));

const MyItem = styled('div')(({theme}) => ({
   display : 'flex',
   alignItems : 'center',
   marginBottom:theme.spacing(4),
   [theme.breakpoints.up("sm")]: {
     marginBottom : theme.spacing(3),
     cursor:"pointer"
  },   
}));

// const MyTypography = styled(Typography)(({theme}) => ({
//   [theme.breakpoints.up("sm")]: {
//     marginBottom : theme.spacing(3),
//     cursor:"pointer"
//  },   
// }));
//  HomeIcon 만 처리했음...

const Leftbar = () => {

   return (
    <MyContainer>
      <MyItem>
        <HomeIcon sx={{ mb: { sm : (theme)=>theme.spacing(2)} ,cursor:'pointer' }} />
        <Typography sx={{ display : {xs :'none', sm:'block'} , fontWeight:'500'}}  >HomePage</Typography>
      </MyItem>
    
      <MyItem>
        <PersonIcon />
        <Typography sx={{ display : {xs :'none', sm:'block'}}}>HomePage</Typography>
      </MyItem>
      <MyItem>
        <ListIcon />
        <Typography sx={{ display : {xs :'none', sm:'block'}}}>HomePage</Typography>
      </MyItem>
      <MyItem>
        <PhotoCameraIcon />
        <Typography sx={{ display : {xs :'none', sm:'block'}}}>HomePage</Typography>
      </MyItem>
      <MyItem>
        <PlayCircleOutlineIcon />
        <Typography sx={{ display : {xs :'none', sm:'block'}}}>HomePage</Typography>
      </MyItem>
      <MyItem>
        <TabletMacIcon />
        <Typography sx={{ display : {xs :'none', sm:'block'}}}>HomePage</Typography>
      </MyItem>
      <MyItem>
        <BookmarkIcon />
        <Typography sx={{ display : {xs :'none', sm:'block'}}}>HomePage</Typography>
      </MyItem>
      <MyItem>
        <StorefrontIcon />
        <Typography sx={{ display : {xs :'none', sm:'block'}}}>HomePage</Typography>
      </MyItem>
      <MyItem>
        <SettingsIcon />
        <Typography sx={{ display : {xs :'none', sm:'block'}}}>HomePage</Typography>
      </MyItem>
      <MyItem>
        <ExitToAppIcon />
        <Typography sx={{ display : {xs :'none', sm:'block'}}}>HomePage</Typography>
      </MyItem>
     
     
    </MyContainer>
  )
}

export default Leftbar